* {
  --webkit-tab-hightlight-color: rgba(#000, 0);
  --webkit-tab-hightlight-color: transparent; }

html {
  height: 100%;
  font-size: 1em; }

body {
  font-size: 16px;
  line-height: 24px;
  background-color: whitesmoke;
  min-height: 100vh;
  min-width: 320px;
  font-family: 'DM Sans', 'Helvetica', sans-serif;
  font-weight: 400; }

p {
  margin-top: 0;
  margin-bottom: 24px; }

b,
strong {
  font-weight: 700; }

ul,
ol {
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

li {
  padding: 0; }

a {
  color: inherit;
  outline: none; }

h6,
h5,
h4,
h3,
h2,
h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: inherit;
  box-sizing: border-box; }

h6 {
  font-size: 18px;
  line-height: 1.333; }

h5 {
  font-size: 20px;
  line-height: 1.2; }

h4 {
  font-size: 22px;
  line-height: 1.091; }

h3 {
  font-size: 24px;
  line-height: 1; }

h2 {
  font-size: 26px;
  line-height: 1.848; }

h1 {
  font-size: 28px;
  line-height: 1.718; }

small {
  display: block;
  font-size: 14px;
  line-height: 1.714;
  margin-bottom: 0; }

::selection {
  background-color: #c6c7c8;
  color: black; }

.app {
  position: relative;
  max-width: 414px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-height: 736px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  user-select: none; }

.hidden,
[hidden] {
  display: none !important;
  visibility: hidden !important; }

.hidden-visibility {
  visibility: hidden;
  opacity: 0; }

.breakline {
  display: block; }

.link {
  cursor: pointer;
  text-decoration: underline; }
  .link:disabled, .link[disabled] {
    cursor: not-allowed; }

.align-middle {
  display: inline-block;
  height: 100%;
  vertical-align: middle; }

.text-center {
  text-align: center; }

.overflow-hidden {
  overflow: hidden; }

.device {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  place-content: center;
  place-items: center;
  background: linear-gradient(120deg, #43cde8, #70d9ee 40%, #ea5580);
  box-sizing: border-box; }

.device-topbar {
  display: flex;
  justify-content: space-between;
  padding: 8px 20px 0;
  color: black;
  background-color: white;
  box-sizing: border-box; }
  .device-topbar .device-time {
    font-weight: 700; }
  .device-topbar .device-group {
    font-size: 0;
    line-height: 1; }
  .device-topbar svg {
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
    margin-bottom: 0;
    width: 20px;
    height: 20px; }

.tabs {
  position: relative;
  display: flex;
  flex-direction: row;
  padding: 0 5px;
  box-sizing: border-box; }
  .tabs .tab-item {
    position: relative;
    margin: 0 5px;
    flex: 1;
    color: #9a9ca4;
    text-align: center;
    overflow: hidden;
    cursor: pointer; }
    .tabs .tab-item span {
      position: relative;
      display: block;
      padding: 8px 4px;
      z-index: 0; }
    .tabs .tab-item.active {
      color: #43cde8; }
      .tabs .tab-item.active::after {
        opacity: 1; }
      .tabs .tab-item.active.leave-right, .tabs .tab-item.active.leave-left {
        color: #9a9ca4; }
      .tabs .tab-item.active.enter-left::after {
        animation-name: tab-enter; }
      .tabs .tab-item.active.leave-left::after {
        animation-name: tab-enter;
        animation-direction: reverse; }
      .tabs .tab-item.active.enter-right::after {
        animation-name: tab-leave;
        animation-direction: reverse; }
      .tabs .tab-item.active.leave-right::after {
        animation-name: tab-leave; }
    .tabs .tab-item::after {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      height: 2px;
      overflow: hidden;
      background-color: #43cde8;
      opacity: 0;
      animation-duration: 400ms;
      animation-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
      animation-fill-mode: both;
      transition: transform 400ms, color 300ms;
      content: "";
      clear: both;
      z-index: 1; }

@keyframes tab-enter {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0%); } }

@keyframes tab-leave {
  from {
    transform: translateX(0%); }
  to {
    transform: translateX(100%); } }

.chat-msg {
  position: relative;
  background-color: #eceff4;
  flex: 1 1 auto;
  margin-bottom: 50px; }

.msg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 24px 16px 5px;
  box-sizing: border-box;
  overflow: auto; }
  .msg-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    padding: 12px;
    border-radius: 16px;
    overflow: hidden;
    background-color: white;
    box-sizing: border-box; }
  .msg-avatar {
    position: relative;
    display: inline-block; }
    .msg-avatar img {
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      object-fit: cover;
      object-position: center; }
    .msg-avatar .icon {
      display: inline-block;
      position: absolute;
      right: 0;
      bottom: 0;
      width: 16px;
      height: 16px;
      border-radius: 100%;
      border: 2px solid white;
      background-color: #43cde8;
      box-sizing: border-box; }
    .msg-avatar .offline {
      background-color: #ea5580; }
  .msg-info {
    position: relative;
    flex: 1;
    padding: 0 12px;
    box-sizing: border-box; }
    .msg-info .info-title {
      font-family: 'DM Sans', 'Helvetica', sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 1.1;
      color: #151624; }
    .msg-info .info-subtitle {
      font-size: 14px;
      color: #151624; }
  .msg-actions .group {
    margin-top: 12px;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .msg-actions .time {
    text-align: right;
    line-height: 1;
    font-size: 14px;
    color: #9a9ca4; }
  .msg-actions .pin {
    display: inline-block;
    color: #9a9ca4;
    margin-right: 6px; }
    .msg-actions .pin svg {
      display: block;
      height: 18px;
      width: 18px;
      margin-bottom: 0; }
  .msg-actions .bullet {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    font-size: 14px;
    text-align: center;
    line-height: 18px;
    color: white;
    overflow: hidden;
    background-color: #ea5580; }

.nav {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: white;
  display: flex;
  padding-top: 2px; }
  .nav-item {
    position: relative;
    padding: 5px;
    flex: 1;
    line-height: 1.4;
    color: #9a9ca4;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box; }
    .nav-item.active {
      color: #43cde8; }
      .nav-item.active svg {
        fill: #43cde8; }
  .nav .icon {
    display: inline-block;
    margin: 0 auto; }
  .nav svg {
    display: block;
    margin-right: auto;
    margin-left: auto;
    width: 20px;
    height: 20px;
    margin-bottom: 0; }
  .nav-label {
    display: block;
    font-size: 12px;
    line-height: 1; }
  .nav .notify {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 50%;
    width: 16px;
    height: 16px;
    border-radius: 100%;
